import uniIcon from "@/components/uni-icons/uni-icons.vue";
export default {
	components:{
		uniIcon
	},
	props:{
		title:{
			type: String,
			default: '请输入'
		}
	},
	data() {
		return {
			bannerShow:false,
			content: ''
		};
	},
	methods:{
		closeBanner:function(){
			this.bannerShow=false;
		},
		show:function(){
			this.bannerShow=true;
		},
		confirmBanner:function(){
			this.$emit('input',this.content)
			this.bannerShow = false
		}
	}
	
}
